/*mobile styles*/

.about {
    display: flex;
    flex-direction: column-reverse;
    align-items: center;
    margin: 40px auto;
}

.about-hero {
    width: 100%;
    height: 300px;
    background-image: url(./../../assets/People.jpg);
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
}

.about-article {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
    background-color: $light-blue;
    box-sizing: border-box;
}

.about-title {
    font-size: 2em;
    text-align: center;
    padding: 10px 20px;
}

.about-decor {
    background-image: url(./../../assets/Decoration.svg);
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
    padding: 40px 80px;
}

.about-text {
    width: 70%;
    text-align: center;
    line-height: 1.5em;
}

.about-sign {
    width: 80px;
    height: 80px;
    background-image: url(./../../assets/Signature.svg);
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
    align-self: flex-end;
    margin-right: 60px;
}

/*tablet styles*/
@media screen and (min-width: 500px) {

    .about-sign {
        align-self: center;
        margin-left: 260px;
    }
}

/*laptop styles*/
@media screen and (min-width: 900px) {
    .about {
        width: 100%;
        flex-direction: unset;
        align-items: center;
    }

    .about-hero {
        width: 50%;
        height: 400px;
    }

    .about-article {
        width: 50%;
        height: 400px;
    }
}